.content_channels{
  text-align: center;
}

.intro{
  margin-bottom: 2%;
}

.channels__title{
  font-weight: 400;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.3);
  margin-bottom: 4%;
}

.channels__acc{
  margin-bottom: 50px;
}

.channels__list, .channels__item{
  padding: 0;
  margin: 0;
  list-style: none;
  text-align: left;
}

.channels__list{
  margin-left: 10%;
}

.channel-acco__descr{
  display: none;
  width: 78%;
  margin-bottom: 4%;
  margin-left: 5%;
  text-align: justify;
}

.channel-acco__trigger-link{
  text-decoration: none;
}

.channel-acco__trigger-title {
  background: linear-gradient(to bottom, rgba(240,240,240,1) 0%, rgba(232,232,232,1) 0%, rgba(232,232,232,1) 0%, rgba(222,222,222,1) 0%, rgba(255,255,255,1) 75%, rgba(255,255,255,1) 100%);
  position: relative;
  border: 1px solid #e6d9e6;
  border-radius: 8px;
  padding: 20px;
  font-size: 18px;
  font-weight: 400;
  font-style: normal;
  color: black;
  margin-bottom: 24px;
  width: 83%;
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.3);
  transition: all 0.5s;

  & i{
    margin-right: 4%;
    font-size: 22px;
  }

  &:hover{
  border: 1px solid #af1212;
  text-shadow: 2px 2px 1px rgba(0, 0, 0, 0.3);
  }

}

.channel-acco__trigger-icon{
  position: absolute;
  bottom: 6%;
  right: 4%;
  width: 38px;

  & img{
    max-width: 100%;
  }
}

.connect{
  text-align: left;
  padding-left: 4%;
  border-top: 1px solid black;
  width: 75%;
  margin-left: 10%;
  margin-top: 5%;
  padding-top: 2%;
  margin-bottom: 5%;

  & p{
    font-weight: 600;
  }

  & i{
    font-weight: 300;
  }
}




