.header {
  position: relative;
  width: 100%;
  height: 150px;
  background-color: #e1f1f5;
  box-shadow: 0px 2px 6px 0px rgba(0, 0, 0, 0.2);
  margin-bottom: 30px; }

.header .container {
  font-size: 0; }

.header__left, .header__right {
  font-size: 16px;
  display: inline-block;
  width: 50%;
  vertical-align: top; }

.logo {
  position: absolute;
  top: 5%;
  left: 13%;
  width: 300px;
  height: 65%;
  color: #3432a7; }
  .logo h1 {
    margin: 0;
    padding: 0;
    font-weight: 400;
    font-style: normal;
    font-size: 48px;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3); }
  .logo span {
    margin: 0;
    padding: 0;
    font-style: italic;
    font-size: 20px;
    text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.3); }

.header__descr {
  position: absolute;
  top: 16%;
  right: 20%;
  width: 42%;
  height: 105px;
  background: url(../../img/futuristic-technology.jpg) 89%;
  background-size: 100%;
  border: 2px solid #3432a7;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5); }

.footer {
  position: relative;
  width: 100%;
  height: 150px;
  background-color: #34636f;
  box-shadow: 0px -1px 6px 0px rgba(0, 0, 0, 0.3);
  margin-top: 50px; }

.footer__adress, .footer__support, .footer__socials {
  position: absolute;
  color: white; }

.footer__adress span, .footer__support span {
  display: block; }

.footer__adress {
  top: 34%;
  left: 16%; }

.footer__support {
  top: 34%;
  left: 50%; }

.content {
  float: right;
  width: 75%; }

.section {
  padding: 0 2% 0 4%;
  margin-bottom: 80px;
  position: relative; }

.section__clients .content__title {
  margin-bottom: 50px; }

.section__img {
  position: absolute;
  width: 65px;
  bottom: 70%;
  right: 6%; }
  .section__img img {
    max-width: 100%; }

.content__title {
  text-align: right;
  padding-right: 20%;
  margin-bottom: 30px;
  font-weight: 400;
  letter-spacing: 2px;
  font-size: 26px; }

.section p {
  text-align: justify; }

.clients__list, .clients__item {
  padding: 0;
  margin: 0; }

.clients__list {
  list-style: none; }

.client__item {
  position: relative;
  display: inline-block;
  width: 248px;
  vertical-align: top;
  text-align: center;
  margin-bottom: 30px;
  margin-right: 23px;
  border: 1px solid #d8c0c0;
  height: 260px;
  border-radius: 25px;
  padding-top: 20px;
  box-shadow: 2px 2px 3px rgba(0, 0, 0, 0.3); }
  .client__item img {
    width: 170px;
    display: block;
    margin: 0 auto; }
  .client__item:after {
    position: absolute;
    top: 0%;
    left: 0%;
    content: "";
    width: 100%;
    height: 100%;
    background-color: rgba(154, 153, 185, 0.1);
    border-radius: 25px; }

.client__logo {
  position: absolute;
  top: 57%;
  left: 50%;
  transform: translate(-50%, -50%); }

.client__title {
  margin: 0 auto;
  margin-bottom: 12px;
  text-align: center;
  width: 90%;
  font-weight: 400;
  font-size: 18px; }

.menu {
  float: left;
  width: 24%;
  border-right: 2px solid #3432a7;
  padding-right: 4%;
  margin-top: 2%;
  height: 223px; }

.nav {
  list-style: none;
  text-align: right;
  font-size: 21px;
