  font-style: normal;
  letter-spacing: 1px;
  color: #3432a7;
  transition: all 0.5s; }
  .nav a {
    text-decoration: none;
    color: #3432a7; }

.nav-item {
  transition: all 0.5s; }

.nav-item-active {
  font-weight: 400;
  text-shadow: 1px 2px 2px rgba(0, 0, 0, 0.3); }

.nav-item:hover {
  font-weight: 400;
  text-shadow: 1px 2px 2px rgba(0, 0, 0, 0.3); }

.content_channels {
  text-align: center; }

.intro {
  margin-bottom: 2%; }

.channels__title {
  font-weight: 400;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.3);
  margin-bottom: 4%; }

.channels__acc {
  margin-bottom: 50px; }

.channels__list, .channels__item {
  padding: 0;
  margin: 0;
  list-style: none;
  text-align: left; }

.channels__list {
  margin-left: 10%; }

.channel-acco__descr {
  display: none;
