.channel-acco__trigger-link {
  text-decoration: none; }

.channel-acco__trigger-title {
  background: linear-gradient(to bottom, #f0f0f0 0%, #e8e8e8 0%, #e8e8e8 0%, #dedede 0%, white 75%, white 100%);
  position: relative;
  border: 1px solid #e6d9e6;
  border-radius: 8px;
  padding: 20px;
  font-size: 18px;
  font-weight: 400;
  font-style: normal;
  color: black;
  margin-bottom: 24px;
  width: 83%;
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.3);
  transition: all 0.5s; }
  .channel-acco__trigger-title i {
    margin-right: 4%;
